import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { JmartCabinetLayoutComponent } from '@jmart-cabinet/components/jmart-cabinet-layout/jmart-cabinet-layout.component';
import { OrdersComponent } from '@jmart-cabinet/modules/orders/orders.component';
import { MainOrdersPageComponent } from '@jmart-cabinet/modules/orders/pages/main-orders-page/main-orders-page.component';


const routes: Routes = [
  {
    path: '',
    component: JmartCabinetLayoutComponent,
    children: [
      {
        path: '',
        component: OrdersComponent,
        children: [
          {
            path: 'main',
            component: MainOrdersPageComponent,
          },
          {
            path: '',
            redirectTo: 'main',
          },
          {
            path: '**',
            redirectTo: 'main',
          },
        ]
      },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class OrdersRoutingModule { }
