import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { OrdersPagination } from '@jmart-cabinet/modules/orders/models/order.model';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
  selector: 'j-order-pagination',
  templateUrl: './order-pagination.component.html',
  styleUrls: ['./order-pagination.component.scss']
})
export class OrderPaginationComponent implements OnInit {
  public page = 1;
  @Input() pagination: OrdersPagination;
  @Output() pageChange: EventEmitter<number> = new EventEmitter<number>();
  constructor(private route: ActivatedRoute, private router: Router) {
    // this.page =  this.route.snapshot.queryParams?.page || 1;
  }

  ngOnInit(): void {
    // this.pageChange.emit(this.page);
  }
  changePage(event){
    console.log(event)
    this.page = event;
    this.pageChange.emit(event);
    this.router.navigate([], {
      queryParams: {
        page: this.page
      },
      queryParamsHandling: 'merge'
    });
  }
}
