import { ChangeDetectionStrategy, Component, Injectable, InjectionToken, Injector, Input, OnInit } from '@angular/core';
import { TableColumnSettings } from '@jmart-cabinet/modules/orders/models/table.model';
import { document } from 'ngx-bootstrap/utils';
import { NodePipe } from '@jmart-cabinet/modules/orders/components/table/node.pipe';

@Injectable({providedIn: 'root'})
export class Table {}
@Component({
  selector: 'j-table',
  templateUrl: './table.component.html',
  styleUrls: ['./table.component.scss'],
  changeDetection: ChangeDetectionStrategy.OnPush
})
export class TableComponent implements OnInit {
  @Input() columnSettings: TableColumnSettings[];
  @Input() data: any[];
  constructor(private injector: Injector) {
  }
  // useValue
  provide(data) {
   return Injector.create({providers: [{provide: Table, useValue: data, deps: []}], parent: this.injector});
  }
  ngOnInit(): void {}

}
