import {
  Component, EventEmitter,
  Input,
  OnChanges,
  OnInit, Output,
  SimpleChanges,
  TemplateRef,
  ViewChild
} from '@angular/core';
import { TableColumnSettings } from '@jmart-cabinet/modules/orders/models/table.model';
import { IOrder } from '@jmart-cabinet/modules/orders/models/order.model';
import { OrderStatusComponent } from '@jmart-cabinet/modules/orders/components/order-status/order-status.component';
import { Router } from '@angular/router';
import { animate, state, style, transition, trigger } from '@angular/animations';
@Component({
  selector: 'j-order-table',
  templateUrl: './order-table.component.html',
  styleUrls: ['./order-table.component.scss'],
  animations: [
    trigger('rotatedState', [
      state('true', style({ transform: 'rotate(0)' })),
      state('false', style({ transform: 'rotate(180deg)' })),
      transition('false => true', animate('200ms ease-out')),
      transition('true => false', animate('200ms ease-in'))
    ])]
})
export class OrderTableComponent implements OnChanges {
  @Input() items: IOrder[];
  @Input() loading: boolean;
  @Input() status = '';
  @Input() descDate: boolean;
  @Output() descDateChange: EventEmitter<boolean> = new EventEmitter<boolean>()
  @ViewChild('orderNumber', {static: true}) orderNumberTemplate!: TemplateRef<any>;
  @ViewChild('orderDate', {static: true}) orderDateTemplate!: TemplateRef<any>;
  @ViewChild('deliveryType', {static: true}) deliveryTypeTemplate!: TemplateRef<any>;
  @ViewChild('totalSum', {static: true}) totalSumTemplate!: TemplateRef<any>;
  @ViewChild('customHeader', {static: true}) customHeaderTemplate!: TemplateRef<any>;

  public columnSettings: TableColumnSettings[];
  readonly deliveryTypes = {
    P: 'Самовывоз',
    DJ: 'Доставка JPost',
    D: 'Доставка'
  };
  readonly emptyText = {
    C: { emptyText: 'У вас пока нет заказов. \n Новые появятся здесь'},
    R: { emptyText: 'У вас пока нет заказов. \n Новые появятся здесь'},
    H: { emptyText: 'Нет обрабатываемых заказов. \n Заказы в обработке появятся здесь'},
    G: { emptyText: 'Нет готовых к выдаче заказов.\n Готовые к выдаче заказы появятся здесь'},
    M: { emptyText: 'Нет доставляемых заказов.\n Доставляемые заказы появятся здесь'},
    Q: { emptyText: 'Нет выданных заказов.\n Выданные заказы появятся здесь'},
    I: { emptyText: 'Нет отмененных заказов.\n Отмененные заказы появятся здесь'},
    B: { emptyText: 'Нет заказов на возврат.\n Заказы в процессе возврата появятся здесь'},
    S: {emptyText: 'Нет возвращенных заказов.\n Возвращенные заказы появятся здесь'},
  };
  readonly theme = { height: '2.5rem', 'background-color': '#ECEEF1', margin: '0.5% 20px', width: 'calc(100% - 40px)' };
  constructor(private router: Router) {
  }
  dateClick(){
    this.descDate = !this.descDate;
    this.descDateChange.emit(this.descDate)
  }
  ngOnChanges(changes: SimpleChanges) {
    if ('status' in changes) {
      if (changes?.status.currentValue){
        this.columnSettings  = [
          {key: 'orderNumber', header: '№ заказа', customTemplate: this.orderNumberTemplate},
          {key: 'orderDate', customHeaderTemplate: this.customHeaderTemplate, customTemplate: this.orderDateTemplate},
          {key: 'city', header: 'Город'},
          {key: 'customer', header: 'Покупатель'},
          {key: 'phone', header: 'Телефон'},
          {key: 'deliveryType', header: 'Тип доставки', customTemplate: this.deliveryTypeTemplate},
          {key: 'totalSum', header: 'Общая сумма', customTemplate: this.totalSumTemplate},
        ];
      } else {
      this.columnSettings  = [
        {key: 'orderNumber', header: '№ заказа', customTemplate: this.orderNumberTemplate},
        {key: 'orderDate', customHeaderTemplate: this.customHeaderTemplate, customTemplate: this.orderDateTemplate},
        {key: 'status', header: 'Статус', customComponent: OrderStatusComponent},
        {key: 'city', header: 'Город'},
        {key: 'customer', header: 'Покупатель'},
        {key: 'phone', header: 'Телефон'},
        {key: 'deliveryType', header: 'Тип доставки', customTemplate: this.deliveryTypeTemplate},
        {key: 'totalSum', header: 'Общая сумма', customTemplate: this.totalSumTemplate},
      ];
      }
    }
  }
}
