import { Component, Input, OnInit } from '@angular/core';
import { FormControl } from '@angular/forms';
import { Observable, Subject } from 'rxjs';
import { ActivatedRoute, Router } from '@angular/router';
import { OrdersService } from '@jmart-cabinet/modules/orders/services/orders.service';
import { map, merge, pluck, scan, share, startWith, switchAll, debounceTime, switchMap, tap, distinctUntilChanged } from 'rxjs/operators';
import { IOrder, IOrdersResult, OrdersPagination } from '@jmart-cabinet/modules/orders/models/order.model';
import { AnalyticsManagerAbstract } from '@analytics/services/manager';

@Component({
  selector: 'j-main-orders-page',
  templateUrl: './main-orders-page.component.html',
  styleUrls: ['./main-orders-page.component.scss']
})
export class MainOrdersPageComponent implements OnInit {
  private searchStream = new Subject<string>();
  private statusStream = new Subject<string>();
  private pageStream = new Subject<number>();
  private descStream = new Subject<boolean>();
  public orderData: IOrder[];
  public pagination: OrdersPagination;
  params = {
    page: 1,
    status: '',
    terms: '',
    descDate: true
  };
  loading = true;
  constructor(private ordersService: OrdersService,
              private route: ActivatedRoute,
              private analytics: AnalyticsManagerAbstract) {
    const params =  this.route.snapshot.queryParams;
    this.params = {
      page: params.page || 1,
      status: params.status || '',
      terms: params.terms || '',
      descDate: params.descDate || true
    };
    this.analytics.logEvent('JMartOrdersList_Start', {version: 1})
  }
  ngOnInit(): void {

    const pageSource = this.pageStream.pipe(
      distinctUntilChanged(),
      map(pageNumber => {
        this.params.page = pageNumber;
        return this.params;
      })
    );
    const searchSource = this.searchStream.pipe(
      debounceTime(1000),
      distinctUntilChanged(),
      map(searchTerm => {
        this.params.terms = searchTerm;
        this.params.page = 1;
        return this.params;
      })
    );
    const statusSource = this.statusStream.pipe(
      distinctUntilChanged(),
      map(statusType => {
        this.params.status = statusType;
        this.params.page = 1;
        return this.params;
      })
    );
    const descSource = this.descStream.pipe(
      distinctUntilChanged(),
      map(descDate => {
        this.params.descDate = descDate;
        this.params.page = 1;
        return this.params;
      })
    );
    const source = pageSource.pipe(
      merge(searchSource, statusSource, descSource),
      startWith(this.params),
      tap(() => {
        this.loading = true;
        this.orderData = null;
      }),
      switchMap((params: {terms: string, page: number, status: '', descDate: boolean}) => {
        return this.ordersService.getOrders(params.terms, params.page, params.status, params.descDate);
      }),
      share(),
      tap(() => this.loading = false)
    );
    source.subscribe(orderData => {
      this.orderData = orderData.items;
      this.pagination = orderData.pagination;
    });

  }
  public searchChange(term: string): void {
    this.searchStream.next(term);
  }
  public statusChange({code, descDate}): void {
    this.params.descDate = descDate;
    this.statusStream.next(code);
  }
  public pageChange(page: number): void {
    this.pageStream.next(page);
  }
  public descChange(descDate: boolean): void {
    this.descStream.next(descDate);
  }
}
